.Contact {
    margin: 0 3rem;
    display: flex;
    justify-content: space-evenly;
    font-size: calc(10px + 2vmin);
    color: black;
    width: 100%;
}

.Contact .profile {
    width: 50%;
    display: flex;
    justify-content: space-evenly;
    height: min-content;
    background-color: #f9ca0840;
    padding: 2em;
    border-radius: 50%;
}

.Contact h4 {
    color: #cf5524;
    font-weight: bold;
    font-style: italic;
  }

.svg-inline--fa {
    padding: 5px;
}

.profile-img {
    width: 40%;
    border-radius: 5%;
    margin: auto;
}

.profile-info {
    height: min-content;
    margin: auto;
}

.contact-form {
    height: min-content;
}

.social {
    display: flex;
}